.bookTable-container{
  display: flex;
    justify-content: center;
    margin-top: 60px;
    flex-direction: column;
    align-content: center;
    align-items: center;

  .bookTable-wrapper {
    display: flex;
    width: 900px;
    padding: 20px;
    border: 1px solid #ccc;
    div {
      flex-basis: 30%;
    }

    
  }

  .bookTable-table-details-wrapper {
    display: flex;
    width: 900px;
    padding: 20px;
    border: 1px solid #ccc;
  }
  .bookTable-details-wrapper {
    display: flex;
    flex-direction: column;
    width: 900px;
    .tableDetails-wrapper {
      
      display: flex;
      div, input {
        flex-basis: 30%;
      }
    }
   
  }
}